.webmain .wrapper .content {
  margin-right: 50px; }
  .webmain .wrapper .content img {
    display: inline; }

.webmain .wrapper .content .header {
  width: 100%;
  margin-bottom: 80px; }

.webmain .wrapper .content .header h1 {
  text-align: center;
